<template>
  <div>
    <h6 v-if="error_message">{{ error_message }}</h6>
    <b-container v-else-if="!success_status" class="p-md-5 p-4 my-md-5 my-0 shadow-sm border-0 rounded bg-white ">
      <div class="forms-logo mb-4">
        <b-img :src="custom_image_url" />
      </div>
      <div v-if="type === 'multi'">
        <div class="steps d-flex p-5 mb-1 position-relative" :class="steps < 2 ? '' : 'justify-content-between'">
          <div :class="step === 1 ? step_class + ' active' : step_class" :id="'step-' + step" v-for="(step, index) in steps" :key="index">
            {{ step }}
          </div>
        </div>
      </div>
      <b-overlay :show="payment_status == 'In Progress'" spinner-variant="primary">
        <Element v-if="!form_response_status" :schema="form_schema" :response_json="form_response_json" ref="formBuilder" @submit="formSubmit" />
        <b-card class="mb-0 shadow-none border-0" v-else body-class="my-3">
          <div class="w-50 mx-auto text-center">
            <i class="display-2 fe fe-check-circle font-weight-light text-success" />
            <div class="mt-3 pt-1">
              <h2 class="mb-2">Your form has been already submitted!</h2>
              <p class="mb-3 text-muted">
                Please contact the owner of the form if you think that this is a mistake
              </p>
            </div>
          </div>
        </b-card>
      </b-overlay>
      <b-card class="mb-0 shadow-none border-0" v-if="no_form_message" body-class="my-3">
        <div class="w-50 mx-auto text-center">
          <i class="display-2 fe fe-search font-weight-light text-gray-500" />
          <div class="mt-2 pt-1">
            <h2 class="mb-2">No forms available</h2>
            <p class="mb-3 text-muted">
              It seems the link you followed has expired
            </p>
          </div>
        </div>
      </b-card>

      <div v-if="recaptcha_error_message">
        <b-card class="mb-0 shadow-none border-0" body-class="my-3">
          <div class="w-50 mx-auto text-center">
            <i class="display-2 fe fe-alert-circle font-weight-light text-warning" />
            <div class="mt-2 pt-1">
              <h2 class="mb-2">Please Recheck Recaptcha</h2>
              <p class="mb-3 text-muted">
                Verification expired please check the checkbox again.
              </p>
            </div>
          </div>
        </b-card>
      </div>
      <div v-if="payment_status != 'In Progress'">
        {{ payment_status }}
      </div>
      <div v-if="validation_all_fields">
        {{ validation_all_fields }}
      </div>
    </b-container>
    <FormSuccess v-if="success_status" :form_details="form_details" />
  </div>
</template>
<script>
  import Element from "./js/Element";
  import formAPI from "@/js/forms/form";
  import Client from "@/js/core/client";
  import FormSuccess from "./FormSuccess";
  import AdvanceTheme from "./js/advance-theme";
  import PaymentMethods from "./js/payment-rules";

  export default {
    name: "Form",
    components: { Element, FormSuccess },
    mixins: [AdvanceTheme],
    props: {
      schema: {
        type: Array,
        required: false,
      },
      add_url: {
        type: String,
        required: false,
      },
    },
    data() {
      return {
        form_schema: { items: [] },
        hasSubbmitted_form: false,
        save_url: "",
        domain_id: "",
        form_id: this.$route.params.form_id ? this.$route.params.form_id : window.id,
        email_notification: false,
        email: "",
        back_click: false,
        custom_image_url: "",
        error_message: "",
        form_details: {},
        success_status: false,
        form_response_status: false,
        multi_form_json: [],
        multi_form_response_json: [],
        form_response_json: {},
        type: "single",
        current_step: 0,
        no_form_message: false,
        // Added for known form display (email or embed)
        display_form_type: "",
        steps: "",
        step_class: "bg-light onhover step rounded-circle rounded-step text-body",
        recaptcha_load: "",
        recaptcha_error_message: false,
        card_details: "",
        Product_price: "",
        customer_details: "",
        payment_status: "",
        request_id: this.$route.query?.r_id,
        tel_required: false,
        validation_all_fields: "",
      };
    },
    async created() {
      // Event listener for ninjasites
      window.addEventListener("message", (event) => {
        if (event.data.type == "form") {
          var height = document.body.offsetHeight;
          event.source.postMessage({ type: "form", height: height, id: event.data.id }, "*");
        }
      });
      if (window.domain) {
        this.getFormDetails();
      } else if (this.schema) {
        this.form_schema = this.schema;
        this.addButton();
      } else {
        this.error_message = "Error";
      }
    },
    async mounted() {
      this.$root.$on("backClick", this.go_back);
    },
    methods: {
      async getFormDetails() {
        this.email = this.$route.params.email ? atob(this.$route.params.email) : "";
        this.display_form_type = this.$route.params.bind ? this.$route.params.bind : "Email";
        let result = await formAPI.getData(this.form_id, this.email);
        result?.[0]?.form_json ? this.parseFormSchema(result) : result.form_response === "true" ? (this.form_response_status = result.form_response) : (this.form_schema = []);
        this.custom_image_url = result[0].custom_logo;
        this.custom_image_url = this.custom_image_url ? this.custom_image_url : "/img/forms-logo.svg";
      },
      parseFormSchema(result) {
        let formData = result[0];
        if (formData.type === "multi") {
          this.multi_form_json = JSON.parse(formData.form_json);
          this.form_schema = this.multi_form_json[0];
          this.type = formData.type;
        } else {
          this.form_schema = JSON.parse(formData.form_json);
          this.removeButton();
        }
        result[0].theme.length > 2 ? this.advance_theme_data(result[0]) : null, (this.form_details = result);
      },
      removeButton() {
        if (this.display_form_type === "email" && this.email === "") {
          let item = this.form_schema.items;
          item.splice(item.length - 1, 1);
        }
      },
      deleteRecaptchaStatus(property) {
        if (property["recaptcha_response_status"] === "static_true") {
          delete property["recaptcha_response_status"];
        }
      },
      async save(submitted_form_response) {
        // Check payment is available or not
        if (this.Product_price || this.card_details || this.customer_details) {
          this.payment_status = "In Progress";
          let payment_status = await this.paymentDetails();
          if (typeof payment_status === "string") {
            this.payment_status = payment_status;
            setTimeout(() => {
              this.payment_status = "";
            }, 1000);
            return false;
          }
          if (payment_status?.status === "succeeded") {
            this.payment_status = "Payment succeeded";
          }
        }
        // To remove the recaptcha static status(This is used for recaptcha tracking)
        try {
          if (this.type === "multi") {
            submitted_form_response.map((property) => {
              this.deleteRecaptchaStatus(property);
            });
          } else if (this.type === "single") {
            this.deleteRecaptchaStatus(submitted_form_response);
          }
        } catch (e) {
          console.log(e);
        }
        // Get client info such as os, and browser type
        let client_info = Client.getVisitorInfo();
        let data = {
          form_response: {
            response: JSON.stringify(submitted_form_response),
            type: this.display_form_type,
            device: client_info.OS,
            browser_type: client_info.browser,
          },
          email_data: {
            subject: "Form Response",
          },
          webhook_data: submitted_form_response,
        };
        // Differentiation between embed and email type
        // eslint-disable-next-line no-undef
        let embed = this.display_form_type === "embed" ? "embed" : "";
        if (this.type === "multi") {
          if (!this.$route.params?.bind) {
            JSON.parse(data.form_response.response).filter((key) => {
              Object.keys(key).filter((item) => {
                if (this.email) return;
                if (item.toLowerCase().includes("email") || item.toLowerCase().includes("mail")) {
                  this.email = key[item];
                }
              });
            });
          }
        } else {
          if (!this.$route.params?.bind) {
            Object.keys(JSON.parse(data.form_response.response)).filter((item) => {
              if (this.email) return;
              if (item.toLowerCase().includes("email") || item.toLowerCase().includes("mail")) return (this.email = JSON.parse(data.form_response.response)[item]);
            });
          }
        }
        let result = await formAPI.saveForm(data, this.form_id, this.email, embed, this.request_id);
        if (result.form_response === "true") this.form_response_status = result.form_response;
        if (result?.status === "Success") {
          if (this.form_details[0].redirect_url) {
            window.location.href = this.form_details[0].redirect_url;
          }
          this.success_status = true;
        } else {
          new Error("This is error message from form submit", result);
        }
      },
      // Validated response json
      validateResponseData(response_data) {
        this.multi_form_response_json.length === 0
          ? this.multi_form_response_json.push(response_data)
          : Object.keys(response_data).length === 1 && !this.multi_form_response_json[this.current_step]
          ? // Purpose: Without required values
            (this.multi_form_response_json[this.current_step] = { empty_key: "empty_value" })
          : !this.multi_form_response_json[this.current_step] && (this.multi_form_response_json[this.current_step] = response_data);
        // To clean previous form data in the element.js file
        this.$children[0].$data.form = { recaptcha_response_status: "static_true" };
      },
      // Checked recaptcha status
      verifyStatus(response) {
        response.recaptcha_response_status === "static_true" ? delete response.recaptcha_response_status : undefined;
      },
      async dataValidate(response) {
        if (this.type === "multi") {
          this.validateResponseData(response);
          // Validated current step and response length
          if (this.multi_form_response_json.length === this.multi_form_json.length && this.current_step + 1 === this.multi_form_response_json.length) {
            this.verifyStatus(response);
            this.save(this.multi_form_response_json);
          } else {
            // Moved to next step
            this.verifyStatus(response);
            this.current_step = this.current_step + 1;
            this.recaptcha_load = "not_loaded";
            this.append_step_class("next");
            this.form_schema = this.multi_form_json[this.current_step];
            this.form_response_json = this.multi_form_response_json[this.current_step];
          }
        } else {
          // For single form
          this.save(response);
        }
      },
      showErrorMessage() {
        this.recaptcha_error_message = true;

        setTimeout(() => {
          this.recaptcha_error_message = false;
        }, 1500);
      },
      // Display message to the user as per the required fields
      validateForm(message) {
        this.validation_all_fields = message;
        setTimeout(() => {
          this.validation_all_fields = "";
        }, 1000);
      },
      formSubmit(response) {
        // Check for all validtion - custom components
        if (this.checkDataType(this.tel_required)) {
          this.validateForm("Please check and validate all required fields");
          return false;
        }
        // checked recaptcha response status
        if (this.recaptcha_load === "loaded") {
          if (response.recaptcha_response_status && response.recaptcha_response_status != "static_true") {
            this.dataValidate(response);
          } else {
            this.showErrorMessage();
          }
          // Verify undefined because while click on next button it will be undefined because we maintain the original response
        } else if (response.recaptcha_response_status === undefined || response.recaptcha_response_status === "static_true" || response.recaptcha_response_status) {
          this.dataValidate(response);
        }
      },
      append_step_class(status) {
        let step_id = document.getElementById(`step-${this.current_step + 1}`);
        step_id.setAttribute("class", status === "next" ? `${this.step_class} active` : `${this.step_class}`);
      },
      go_back(response) {
        this.back_click = true;
        if (this.current_step - 1 < 0) {
          this.no_form_message = true;
          setTimeout(() => {
            this.no_form_message = false;
          }, 1500);
        } else {
          this.validateResponseData(response);
          this.append_step_class("back");
          // Moved to back step
          this.current_step = this.current_step - 1;
          this.form_schema = this.multi_form_json[this.current_step];
          this.form_response_json = this.multi_form_response_json[this.current_step];
        }
      },
      recaptcha_loaded() {
        this.recaptcha_load = "loaded";
      },
      paymentDetails() {
        return this.validate(this.card_details, this.Product_price, this.customer_details);
      },
      receivePaymentDetails(card_details, amount, customer_details) {
        setTimeout(() => {
          this.card_details = card_details;
          this.Product_price = amount;
          this.customer_details = customer_details;
        }, 500);
      },
      ...PaymentMethods,
      // Validate the tel field
      validatePhone(number) {
        this.tel_required = number;
      },
      // Check wheather the argument is boolean/string/undefined - Based upon that validate
      checkDataType(str) {
        if (typeof str === "boolean") {
          return str;
        }
        return false;
      },
    },

    beforeUpdate() {
      this.$root.$on("recaptcha_loaded", this.recaptcha_loaded);
      this.$root.$on("payment_details", this.receivePaymentDetails);
      this.$root.$on("tel_required", this.validatePhone);
      this.$root.$on("location_required", this.validatePhone); // Validation check
      this.$root.$on("rating_required", this.validatePhone);
    },
    watch: {
      form_schema: {
        handler(newValue) {
          this.form_schema = newValue;
        },
      },
      multi_form_json: {
        immediate: true,
        handler(newvalue) {
          this.steps = newvalue.length;
        },
        deep: true,
      },
    },
  };
</script>
<style scoped>
  .rounded-step {
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 1.99;
    z-index: 2;
  }
  .steps::before {
    content: "";
    border-top: 1px dashed #e3e5e8 !important;
    top: 50%;
    width: 91.5%;
    position: absolute;
    margin-left: 17px;
  }
  .bg-light.rounded-step.active {
    background-color: #2c7be5 !important;
    border-color: #2c7be5 !important;
  }
  .bg-light.rounded-step.active a {
    color: #fff !important;
  }
</style>
