<template>
  <b-container class="p-5 mt-2 border rounded">
    <b-row>
      <b-col md="12">
        <b-card no-body class="shadow-none borde-0 mb-0 text-center pt-5">
          <i class="display-2 fe fe-check-circle font-weight-light text-success"/>
          <b-card-body class="pt-0" v-html="success_message"/>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  export default {
    name: "FormSuccess",
    props: {
      form_details: {
        type: Array,
        default: () => [],
      },
    },
    data() {
      return {
        success_message: '<h2 class="pt-3 mb-3">Thank You!</h2><h4 class="mb-4 text-muted pb-2">Your response has been submitted.</h4>',
      };
    },
    created() {
      this.success_message = this.form_details[0]?.response_message ? this.form_details[0].response_message : this.success_message;
    },
  };
</script>
